import React from 'react';
import { Typography, Divider, Carousel } from 'antd';
import ImageMapper from 'react-image-mapper';
import { landingPage as cms } from '../../cms';
import { landingPageCarousel, altDisAssebly } from '../../config';
const { Title, Paragraph, Text } = Typography;

const alternatorCords = [
  { coords: [148, 272], href: "#alternator_part_8", tooltip: cms["subHeading8"] },
  { coords: [206, 276], href: "#alternator_part_7", tooltip: cms["subHeading7"] },
  { coords: [314, 280], href: "#alternator_part_3", tooltip: cms["subHeading3"] },
  { coords: [315, 197], href: "#alternator_part_6", tooltip: cms["subHeading6"] },
  { coords: [306, 133], href: "#alternator_part", tooltip: cms["subHeading"] },
  { coords: [415, 283], href: "#alternator_part_4", tooltip: cms["subHeading4"] },
  { coords: [476, 284], href: "#alternator_part_3", tooltip: cms["subHeading3"] },
  { coords: [417, 107], href: "#alternator_part_", tooltip: cms["subHeading"] },
  { coords: [517, 46], href: "#alternator_part_1", tooltip: cms["subHeading1"] },
  { coords: [461, 202], href: "#alternator_part_2", tooltip: cms["subHeading2"] }]

class LandingPage extends React.Component {
  displayPartsDetails = () => {
    return [1, 2, 3, 4, 5, 6, 7, 8].map(value => (
      <React.Fragment key={value}>
        <Title id={`alternator_part_${value}`} level={4} underline>{cms[`subHeading${value}`]}</Title>
        <Paragraph>{cms[`subText${value}`]}</Paragraph>
      </React.Fragment>
    ))
  }

  getMappingCoordinates = () => {
    return {
      areas: alternatorCords.map(({ coords, href, _id }) => ({
        shape: 'circle',
        coords: [...coords, 8],
        preFillColor: "red",
        href,
      })),
      name: 'abc'
    }
  }

  render() {
    return (
      <Typography>
        <Title>
          {cms.title}
        </Title>
        <Carousel autoplay>
          {landingPageCarousel.map(imageUrl => <img src={imageUrl} key={imageUrl} alt="N/A" />)}
        </Carousel>
        <Title level={4} align='center'>{cms.altWorkingHeading}</Title>
        <div id="alt-assembly-outer-div" style={{ margin: '1em 0px' }}>
          <div style={{ maxHeight: 525, maxWidth: 700, display: 'block', margin: 'auto' }} >
            <ImageMapper
              src={altDisAssebly}
              height={524}
              width={700}
              map={this.getMappingCoordinates()}
              // onMouseEnter={(area, index, event) => console.log(">>>", area, index, event)}
            />
          </div>
          {/* <img src={altDisAssebly} alt="N/A" style={{ maxHeight: 525, maxWidth: 700, display: 'block', margin: 'auto' }} /> */}
        </div>
        <Paragraph>{cms.altPara1}</Paragraph>
        <Paragraph>{cms.altPara2}</Paragraph>
        <Paragraph>{cms.altPara3}</Paragraph>
        {this.displayPartsDetails()}
      </Typography>
    )
  }
}

export default LandingPage;
